"use client";

import React, { useEffect, useMemo, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import axios from "axios";
import { toast } from "react-toastify";

import extractErrorMessages from "@/src/app/utils/error.utils";
import { QuoteService } from "@/src/services/quote.service";
import { QuotationStatus } from "@/src/constants/enum";
import { Quotation, QuotationUpdatePayload } from "@/src/types/quotations/quote.types";

import Input from "@/src/components/ui/Input";
import Select from "@/src/components/ui/Select";
import ErrorComponent from "@/src/components/ui/ErrorComponent";

import {
  IoSaveOutline,
  IoArrowBack,
  IoDocumentText,
  IoInformationCircleOutline,
  IoCashOutline,
  IoCalendarOutline,
} from "react-icons/io5";

const Page = () => {
  const { id } = useParams();
  const router = useRouter();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string[] | string>("");

  const [original, setOriginal] = useState<Quotation | null>(null);
  const [formData, setFormData] = useState<QuotationUpdatePayload>({});

  /* ---------------- FETCH QUOTATION ---------------- */
  const fetchQuotation = async () => {
    try {
      const res = await QuoteService.get_by_id(String(id));
      const data: Quotation = res.data.data;

      setOriginal(data);

      setFormData({
        title: data.title,
        description: data.description,
        issueDate: data.issueDate,
        valid: data.valid,
        subTotal: data.subTotal,
        total: data.total,
        quotationStatus: data.quotationStatus,
      });
    } catch (err) {
      if (axios.isAxiosError(err)) {
        setError(extractErrorMessages(err));
      } else {
        setError("Something went wrong");
      }
    }
  };

  useEffect(() => {
    if (!id) return;
    fetchQuotation();
  }, [id]);

  /* ---------------- DIFF PAYLOAD ---------------- */
  const diffPayload = useMemo(() => {
    if (!original) return {};

    const payload: QuotationUpdatePayload = {};

    Object.entries(formData).forEach(([key, value]) => {
      const originalValue = (original as any)[key];
      if (value !== originalValue) {
        (payload as any)[key] = value;
      }
    });

    return payload;
  }, [formData, original]);

  const hasChanges = Object.keys(diffPayload).length > 0;

  /* ---------------- HANDLERS ---------------- */
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    if (error) setError("");
  };

  const handleNumberChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: Number(value) }));
  };

  const handleUpdate = async () => {
    if (!hasChanges) {
      toast.info("No changes to update");
      return;
    }

    setLoading(true);
    setError("");

    try {
      await QuoteService.update(String(id), diffPayload);
      toast.success("Quotation updated successfully");
      router.push(`/quotations/view/${id}`);
    } catch (err) {
      if (axios.isAxiosError(err)) {
        const msg = extractErrorMessages(err);
        setError(msg);
        toast.error(`${msg}`);
      } else {
        setError("Something went wrong");
      }
    } finally {
      setLoading(false);
    }
  };

  if (!original) return null;

  /* ---------------- UI ---------------- */
  return (
    <div className="ml-72 mt-14 p-6 min-h-screen">
      <div className="mb-6 flex items-center justify-between">
        <h1 className="text-3xl font-bold flex items-center gap-3">
          <IoDocumentText className="text-blue-600" />
          Update Quotation
        </h1>

        <button
          onClick={() => router.back()}
          className="flex items-center gap-2 text-gray-600 hover:text-gray-800"
        >
          <IoArrowBack />
          Back
        </button>
      </div>

      <ErrorComponent error={error} />

      {/* BASIC INFO */}
      <section className="bg-white rounded-xl border p-6 mb-6">
        <h2 className="text-xl font-semibold flex items-center gap-2 mb-4">
          <IoInformationCircleOutline />
          Basic Information
        </h2>

        <Input
          label="Title"
          name="title"
          value={formData.title || ""}
          onChange={handleChange}
        />

        <Input
          type="textarea"
          label="Description"
          name="description"
          value={formData.description || ""}
          onChange={handleChange}
        />

        <Select
          label="Status"
          name="quotationStatus"
          value={formData.quotationStatus}
          onChange={handleChange}
          options={Object.values(QuotationStatus).map(s => ({
            label: s.charAt(0).toUpperCase() + s.slice(1),
            value: s,
          }))}
        />
      </section>

      {/* FINANCIAL */}
      <section className="bg-white rounded-xl border p-6 mb-6">
        <h2 className="text-xl font-semibold flex items-center gap-2 mb-4">
          <IoCashOutline />
          Financial Details
        </h2>

        <Input
          type="number"
          label="Subtotal"
          name="subTotal"
          value={formData.subTotal ?? 0}
          onChange={handleNumberChange}
        />

        <Input
          type="number"
          label="Total"
          name="total"
          value={formData.total ?? 0}
          onChange={handleNumberChange}
        />
      </section>

      {/* DATES */}
      <section className="bg-white rounded-xl border p-6 mb-6">
        <h2 className="text-xl font-semibold flex items-center gap-2 mb-4">
          <IoCalendarOutline />
          Validity
        </h2>

        <Input
          type="date"
          label="Issue Date"
          name="issueDate"
          value={formData.issueDate || ""}
          onChange={handleChange}
        />

        <Input
          type="date"
          label="Valid Until"
          name="valid"
          value={formData.valid || ""}
          onChange={handleChange}
        />
      </section>

      {/* ACTIONS */}
      <div className="flex justify-end gap-4">
        <button
          disabled={!hasChanges || loading}
          onClick={handleUpdate}
          className="px-6 py-3 bg-blue-600 text-white rounded-lg flex items-center gap-2 disabled:opacity-50"
        >
          <IoSaveOutline />
          {loading ? "Updating..." : "Update Quotation"}
        </button>
      </div>
    </div>
  );
};

export default Page;
